@keyframes wave {
  0%, 100% { transform: rotate(0deg); }
  25% { transform: rotate(-15deg); }
  75% { transform: rotate(15deg); }
}

@keyframes light-sweep {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

@keyframes pulsate {
  0%, 100% { opacity: 1; transform: scale(1); }
  50% { opacity: 0.8; transform: scale(1.05); }
}

@keyframes gradient-flow {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

@keyframes glitch {
  0% {
    transform: translate(0);
    text-shadow: none;
  }
  20% {
    transform: translate(-2px, 2px);
    text-shadow: 2px 2px #ff3e00;
  }
  40% {
    transform: translate(-2px, -2px);
    text-shadow: 2px -2px #ff3e00;
  }
  60% {
    transform: translate(2px, 2px);
    text-shadow: -2px 2px #ff3e00;
  }
  80% {
    transform: translate(2px, -2px);
    text-shadow: -2px -2px #ff3e00;
  }
  100% {
    transform: translate(0);
    text-shadow: none;
  }
}

.wave-animation {
  animation: wave 2s infinite;
  transform-origin: 70% 70%;
}

.light-sweep::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.1), transparent);
  animation: light-sweep 3s infinite;
}

.pulsate {
  animation: pulsate 2s infinite;
}

.gradient-border {
  background: linear-gradient(45deg, var(--primary), var(--primary-light));
  background-size: 200% 200%;
  animation: gradient-flow 3s infinite;
}

.perspective-1000 {
  perspective: 1000px;
}

.preserve-3d {
  transform-style: preserve-3d !important;
  -webkit-transform-style: preserve-3d !important;
}

.backface-hidden {
  backface-visibility: hidden !important;
  -webkit-backface-visibility: hidden !important;
}

.rotate-y-180 {
  transform: rotateY(180deg) !important;
  -webkit-transform: rotateY(180deg) !important;
}

.shadow-glow {
  box-shadow: 0 0 15px rgba(var(--color-primary-rgb), 0.5);
}

.project-image-enter {
  opacity: 0;
  transform: scale(0.95);
}

.project-image-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 300ms, transform 300ms;
}

.project-image-exit {
  opacity: 1;
}

.project-image-exit-active {
  opacity: 0;
  transition: opacity 300ms;
}

.glitch-text {
  animation: glitch 1s infinite;
  animation-timing-function: steps(2, end);
}

.particle {
  pointer-events: none;
  position: fixed;
  background: radial-gradient(circle, #ff3e00 0%, transparent 100%);
  border-radius: 50%;
}

/* Markdown Styles */
.prose-dark {
  color: #e5e7eb;
}

.prose-dark h1,
.prose-dark h2,
.prose-dark h3,
.prose-dark h4 {
  color: #ff3e00;
  margin-top: 1.5em;
  margin-bottom: 0.5em;
}

.prose-dark p {
  margin: 0.5em 0;
}

.prose-dark ul,
.prose-dark ol {
  margin: 0.5em 0;
  padding-left: 1.5em;
}

.prose-dark code {
  color: #ff5e2c;
  background: rgba(255, 62, 0, 0.1);
  padding: 0.2em 0.4em;
  border-radius: 0.25em;
}

.prose-dark pre {
  background: rgba(0, 0, 0, 0.2);
  padding: 1em;
  border-radius: 0.5em;
  overflow-x: auto;
}

.prose-dark a {
  color: #ff3e00;
  text-decoration: underline;
}

.prose-dark blockquote {
  border-left: 3px solid #ff3e00;
  padding-left: 1em;
  margin: 1em 0;
  font-style: italic;
}
